body .black-bg {
  background: black;
  height: 100%;
  width: 100%;
  position: absolute;
  opacity: 0.4;
}
body .logo {
  width: 4rem;
}
body .logo img {
  width: 100%;
}

header {
  display: flex;
  justify-content: space-between;
}
header .nav-ul {
  display: flex;
  border: 1px solid red;
  padding: 0;
  list-style-type: none;
}
header .nav-ul a {
  text-decoration: none;
}

main {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 90vw;
  padding: 1em;
}

.hero {
  color: #f1faee;
}
.hero-title {
  font-size: 15vmin;
  margin: 0;
  line-height: 1em;
}
.hero-desc {
  font-size: 1.5rem;
}

.btns-container .btn {
  font-size: 1.2rem;
  outline: none;
  cursor: pointer;
  text-transform: uppercase;
  padding: 1em 2em;
  border-radius: 0.4rem;
  transition: 0.3s;
  border: none;
  font-weight: 700;
  -webkit-box-reflect: below 4px linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.4));
}
.btns-container .cta {
  color: #f1faee;
  background-color: #1d3557;
  box-shadow: 0px 0px 60px #1f4c65;
}
.btns-container .cta:active {
  scale: 0.92;
}
.btns-container .cta:hover {
  background: #1d3557;
  background: linear-gradient(270deg, rgba(2, 29, 78, 0.6392156863) 0%, #a8dadc 60%);
  color: rgb(4, 4, 38);
}
.btns-container .login {
  color: #f1faee;
  padding: 1em 2.5em;
  background: transparent;
  border: 2px solid #1d3557;
}